import axios from "axios";
import { endpoints } from "Constants/api";
import axiosHelpers from "Constants/axiosHelpers";

export const getPropertyListDetails = async (
  approvalStatus,
  type,
  primaryPOC,
  secondaryPOC,
  area,
  city,
  page,
  name,
  limit
) => {
  const request = await axios({
    method: "get",
    url: endpoints.propertyList.GET_PROPERTY_LIST,
    params: {
      approvalStatus,
      type,
      primaryPOC,
      secondaryPOC,
      area,
      city,
      page,
      name,
      limit,
    },
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
  }).catch(axiosHelpers);
  return request;
};
export const deletePropertyList = async (id) => {
  const request = await axios({
    method: "delete",
    url: endpoints.propertyList.DELETE_PROPERTY_LIST,
    params: {
      id,
    },
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
  }).catch(axiosHelpers);
  return request;
};

export const deActivatePropertyList = async (body) => {
  const request = await axios({
    method: "patch",
    url: endpoints.propertyList.DEACTIVATE_PROPERTY,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    data: body,
  });
  return request;
};
export const getPropertyNotificationDetails = async (propertyId) => {
  const request = await axios({
    method: "get",
    url: endpoints.propertyList.NOTIFICATION_LIST,
    params:{
      propertyId
    },
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
  }).catch(axiosHelpers);
  return request.data;
};

export const readPropertyNotificationDetails = async (body) => {
  const request = await axios({
    method: "post",
    url: endpoints.propertyList.READ_NOTIFICATION,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    data:body
  }).catch(axiosHelpers);
  return request.data;
};
